import React, { Component } from "react";
import "./App.css";
import axios from "axios";

export class App extends Component {
    state = {
        name: "",
        productId: "",
        description: "",
        price: "",
        updName: "",
        updProductId: "",
        updDescription: "",
        updPrice: "",
        delProductId: "",
        products: [],
    };

    componentDidMount = () => {
        this.getProducts();
    };

    handleChange = ({ target }) => {
        const { name, value } = target;
        this.setState({ [name]: value });
    };

    handleChangeForInt = ({ target }) => {
        const { name, value } = target;
        const intvalue = parseInt(value);
        this.setState({ [name]: intvalue });
    };

    getProducts = () => {
        axios
            .get("/api/products")
            .then((response) => {
                const data = response.data;
                this.setState({ products: data });
                console.log("Data has been received!!");
            })
            .catch(() => {
                alert("Error retrieving data!!!");
            });
    };

    resetUserInputs = () => {
        this.setState({
            name: "",
            description: "",
            price: "",
            productId: "",
        });
    };

    create = (event) => {
        event.preventDefault();

        const payload = {
            name: this.state.name,
            productId: this.state.productId,
            description: this.state.description,
            price: this.state.price,
        };

        axios({
            url: "/api/products/",
            method: "POST",
            data: payload,
        })
            .then(() => {
                console.log("Data has been sent to the server");
                this.resetUserInputs();
                this.getBlogPost();
            })
            .catch((err) => {
                console.log(err);
            });

        console.log(this.state);
    };

    displayProducts = (products) => {
        if (!products.length) return null;

        return products.map((product, index) => (
            <div key={index} className='product'>
                <br></br>
                <h3>
                    <strong>{product.productId}</strong> {product.name}
                </h3>
                <p>{product.description}</p>
                <p>Price: &#8377;{product.price}</p>
            </div>
        ));
    };

    render() {
        return (
            <div>
                <h2>Welcome to my CRUD App FrontEnd</h2>
                <form onSubmit={this.create}>
                    <div className='form-input'>
                        <input
                            type='text'
                            name='name'
                            placeholder='Enter Product Name'
                            value={this.state.name}
                            onChange={this.handleChange}
                        />
                    </div>
                    <div className='form-input'>
                        <input
                            type='text'
                            name='productId'
                            placeholder='Enter Product ID'
                            value={this.state.productId}
                            onChange={this.handleChangeForInt}
                        />
                    </div>
                    <div className='form-input'>
                        <textarea
                            name='description'
                            placeholder='Enter Product Description'
                            cols='30'
                            rows='5'
                            value={this.state.description}
                            onChange={this.handleChange}
                            spellCheck='false'
                        ></textarea>
                    </div>
                    <div className='form-input'>
                        <input
                            type='text'
                            name='price'
                            placeholder='Enter Product Price'
                            value={this.state.price}
                            onChange={this.handleChangeForInt}
                        />
                    </div>

                    <button>Create Product</button>
                </form>

                <div className='product'>
                    <h2>Products List</h2>
                    {this.displayProducts(this.state.products)}
                </div>
            </div>
        );
    }
}

export default App;
